/* SLIDER */

.Slider {
  overflow: hidden;
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
}

.Slider-slides {
  display: -ms-flexbox;
  display: flex;
}

.Slider-slides--transition {
  -webkit-transition: -webkit-transform 0.25s;
  transition: -webkit-transform 0.25s;
  -o-transition: transform 0.25s;
  transition: transform 0.25s;
  transition: transform 0.25s, -webkit-transform 0.25s;
}

.Slider-slides > * {
  -ms-flex-preferred-size: 0;
      flex-basis: 0;
  -ms-flex-positive: 1;
  flex-grow: 1;
}

.Slider-slides > * {
  -ms-flex-preferred-size: 0;
      flex-basis: 0;
  -ms-flex-positive: 1;
  flex-grow: 1;
}

.Slider-nav {
  -ms-flex-line-pack: center;
      align-content: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  margin-top: 10px;
  -ms-flex-order: 2;
  order: 2;
}

.Slider-navButton {
  background: none;
  border: 0;
  height: 20px;
  opacity: 0.5;
  outline: none;
  position: relative;
  -webkit-transition: opacity 0.25s;
  -o-transition: opacity 0.25s;
  transition: opacity 0.25s;
  width: 20px;
  padding: 0;
}

.Slider-navButton::after {
  background: #345;
  border-radius: 50%;
  content: '';
  display: block;
  height: 6px;
  left: 7px;
  position: absolute;
  top: 7px;
  width: 6px;
}

.Slider-navButton--active {
  opacity: 1;
}

.Slider-navButton--active::after {
  height: 12px;
  left: 4px;
  opacity: 1;
  top: 4px;
  width: 12px;
}

.Slider-arrows {
  height: 0;
  left: 0;
  position: absolute;
  top: calc(50% - 35px);
  width: 100%;
  z-index: 1;
}

.Slider-arrows--noNav {
  top: calc(50% - 20px);
}

.Slider-arrow {
  background: none;
  border: none;
  height: 40px;
  opacity: 0.5;
  outline: none;
  position: absolute;
  -webkit-transition: opacity 0.25s;
  -o-transition: opacity 0.25s;
  transition: opacity 0.25s;
  padding: 0px;
  width: 21px;
}

.Slider-arrow:hover {
  opacity: 1;
}

.Slider-arrow::after, .Slider-arrow::before {
  background: #fff;
  border-radius: 3px;
  content: '';
  display: block;
  height: 23px;
  position: absolute;
  width: 5px;
  left: 8px;
}

.Slider-arrow::before {
  top: 0;
}

.Slider-arrow::after {
  bottom: 0;
}

.Slider-arrow--left {
  top: 0;
  left: 10px;
}

.Slider-arrow--left::before {
  -webkit-transform: rotate(30deg);
          -ms-transform: rotate(30deg);
      transform: rotate(30deg);
}

.Slider-arrow--left::after {
  -webkit-transform: rotate(-30deg);
          -ms-transform: rotate(-30deg);
      transform: rotate(-30deg);
}

.Slider-arrow--right {
  top: 0;
  right: 10px;
}

.Slider-arrow--right::before {
  -webkit-transform: rotate(-30deg);
          -ms-transform: rotate(-30deg);
      transform: rotate(-30deg);
}

.Slider-arrow--right::after {
  -webkit-transform: rotate(30deg);
          -ms-transform: rotate(30deg);
      transform: rotate(30deg);
}


